.cards {
    margin-top: 5%;
    display: flex;
    justify-content: space-between;
}

.first-card {
    background-color: white;
    border-radius: 31px;
    width: 100%;
}

.first-card-child {
    display: flex;
    align-items: center;

}

.first-card-child {
    h1 {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 50px;
        line-height: 97.9%;

        display: flex;
        align-items: flex-end;

        color: #000000;
    }
}

.first-card-child {
    p {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 31px;
        line-height: 99.4%;

        display: flex;
        align-items: flex-end;

        color: #000000;
    }
}

.first-card-child {
    h6 {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 36px;



        color: #00AF00;
    }
}

.child-text {
    padding-left: 10px;
}

.child-img {
    padding-top: 20px;
}

.child-img {
    img {
        width: 300px;
        height: 401px;
    }
}

.second-card {
    height: 100%;
    background-color: white;
    border-radius: 31px;
}

.second-card {
    h1 {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 40px;
        line-height: 97.9%;
        /* or 39px */


        color: #000000;
        padding-left: 20px;
    }
}

.second-card {
    input {
        margin-left: 20px;
        border: 4px solid #000000;
        border-radius: 23px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 103.9%;


        color: rgba(0, 0, 0, 0.82);
        width: 550px;
        padding-left: 30px;
        height: 75px;
        margin-top: 5%;
    }
}

.second-card-btn {
    text-align: center;
}

.second-card-btn {
    button {
        background: #00AF00;
        margin: 20px 0px;
        color: white;
        width: 400px;
        height: 50px;
        border: none;
        font-size: 25px;
        font-weight: 700;
        border-radius: 50px;
        transition: 0.5s ease;
    }
}

.second-card-btn {
    button:hover {
        color: black;
        background: white;
    }

}

@media only screen and (max-width: 991px)  {
    .cards{
        display: block;
    }
    .first-card{
        width: 100% !important;
        height: 100% !important;
        .child-img {
            img{
                width: 100% !important;
                height: 100% !important;
                border-radius: 20px;
            }
        }
        .child-text {
          h1{
            font-size: 30px;
          }
          p{
            font-size: 20px;
          }
          h6{
            font-size: 25px;
          }
        }
    }
    .second-card{
        width: 100% !important;
        height: 100% !important;
        margin-top: 3%;
        input{
            width: 100% !important;
            margin-left: 0% !important;
        }
        .second-card-btn {
            button{
                width: 100% !important;
            }
        }
    }
    
  }
  