.info {
  display: flex;
  justify-content: space-around;
  margin-top: 10%;
  align-items: center;
  background-color: #d9d9d9;
  border-radius: 51px;
  padding-top: 25px;
  padding-bottom: 15px;
  color: black;
}

.border {
  display: none;
  background-color: black;
  color: black;
  width: 4px;
  height: 80px;
}

.info-first {
  border-right: 1px solid black;
  padding-right: 50px;
  h6 {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    // line-height: 34px;

    color: #000000;
  }
}

.info-first {
  
  h6 {
    span {
      margin-right: 10px;
      margin-bottom: 3px;
    }
  }
}

.info-first {
  p {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    // line-height: 29px;
    text-align: center;

    color: #000000;
  }
}

