@media only screen and (max-width: 991px) {
  .info {
    display: block !important;
    width: 100% !important;
    height: 100% !important;
    text-align: center;
    align-items: center;
    margin-bottom: 5%;
    justify-content: center;
  }
  .info-first {
    border-bottom: 1px solid black;
    padding: 0;
    // width: 90%;
    border-right: none !important;
  }
  .info-second{
    padding: 0;
    border-bottom: 1px solid black;
    border-right: none !important;

  }
