footer{
  margin-top: 10%;
}
.footer-content {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.first-footer {
  img {
    width: 20%;
  }
  h1 {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 25px;
    line-height: 49px;

    display: flex;
    align-items: flex-end;

    color: #b1b1b1;
  }
}
.sec-footer {
  h6 {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 37px;
    /* identical to box height */

    display: flex;
    align-items: flex-end;

    color: #b1b1b1;
  }
}
.third-footer {
  h6 {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 37px;

    display: flex;
    align-items: flex-end;

    color: #b1b1b1;
  }
}
