.contact {
    margin-top: 150px;
    margin-bottom: 70px;

    .container {
        .back {
            width: 100%;
            height: 300px;
            background: #d9d9d9;
            border-radius: 30px;
            padding: 30px;
            display: flex;
            justify-content: space-between;
            align-items: center;

            .left {
                width: 60%;

                h1 {
                    color: #000000;
                    font-weight: 700;
                    font-size: 30px;
                    margin: 20px 0px;
                }

                p {
                    color: #000000;
                    font-weight: 5  00;
                    font-size: 20px;
                }
            }

            .right {
                width: 35%;
                margin: 0px 20px;

                .name {
                    width: 100%;
                    height: 62px;
                    border: 2px solid #000000;
                    border-radius: 23px;
                    background: transparent;
                    text-align: start;
                    padding: 25px;
                }

                .name::placeholder {
                    font-weight: 600;
                    color: black;
                }

                .bottom {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin: 20px 0px;

                    .tel {
                        width: 47%;
                        height: 62px;
                        border: 2px solid #000000;
                        border-radius: 23px;
                        background: transparent;
                        text-align: start;
                        padding: 20px;
                        font-weight: 600;
                    }
                    .select{
                        font-weight: 600;
                        color: #636363;
                        height: 62px;
                        border: 2px solid black;
                        border-radius: 23px;
                        background: transparent;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        font-weight: 700;
                        font-size: 15px;
                        padding: 20px;
                    }
                }

                .button {
                    width: 100%;
                    height: 50px;
                    border-radius: 23px;
                    border: 2px solid #000000;
                    background: transparent;
                    font-size: 17px;
                    font-weight: 700;
                    transition: 0.2s ease;
                }

                .button:hover {
                    background: #00AF00;
                    color: white;
                }
            }
        }
    }
}