nav {
  padding: 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .container {
    display: flex;
    justify-content: space-between;

    img {
      width: 80px;
      height: 80px;
    }

    .collapse {
      display: flex;
      align-items: center;

      .div {
        width: 15%;
      }

      ul {
        text-align: center;

        a {
          text-decoration: none;

          font-size: 18px;
          margin: 0px 20px;
          color: #ffff;
          font-weight: 400;

          &.active {
            color: #00af00;
            font-weight: 400;
          }
        }
      }

      a {
        button {
          font-weight: 600;
          color: white;
          font-size: 20px;
        }

        button:hover {
          color: #00af00;
        }
      }
      li {
        list-style: none;
        .css-i4bv87-MuiSvgIcon-root {
          font-size: 30px;
        }
      }
      .side-menu {
        background: rgba(255, 255, 255, 0.463);
        width: 340px;
        height: 100vh;
        display: flex;
        flex-direction: column;
        padding: 20px;
        position: fixed;
        top: 0;
        right: -100%;
        transition: 900ms;
      }

      .side-menu.active {
        z-index: 9999;
        right: 0;
        transition: 950ms;
        --webkit-transition: 950ms;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
      }

      .closeIcon {
        color: #171a20;
        align-self: end;
        transition: 0.5s ease;
      }

      .closeIcon:hover {
        transform: rotate(180deg);
        cursor: pointer;
      }

      .sideBarLinks {
        width: 300px;
        cursor: pointer;
        list-style: none;
        font-family: "Godham", sans-serif;
        font-weight: 600;
        font-size: 15px;
        padding: 5px;
        text-align: center;
        transition: 0.5s ease;
        --webkit-transition: 0.5s ease;
        border-radius: 5px;
      }

      .sidebarNav {
        width: 240px;
      }

      .sideBarLinks:hover {
        background-color: #00af00;
        border-radius: 10px;
      }
    }
  }
}

@media only screen and (max-width: 991px) and (-webkit-device-pixel-ratio: 2) {
  .nav-item {
    display: none;
  }
  
}
