.info {
    display: flex;
    justify-content: space-around;
    margin-top: 10%;
    align-items: center;
    background-color: #D9D9D9;
    border-radius: 51px;
    padding-top: 25px;
    padding-bottom: 15px;
    color: black;
}

.border {
    background-color: black;
    color: black;
    width: 4px;
    height: 80px;
}

.info-one {
 
    h6 {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 25px;
        // line-height: 34px;
        display: flex;
        align-items: flex-end;

        color: #000000;
    }



}

.info-one {
    h6 {
        span {
            margin-right: 10px;
            margin-bottom: 3px;
        }
    }
}

.info-one {
    p {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        // line-height: 29px;
        text-align: center;

        color: #000000;
    }

}


