.front-header {
    margin-top: 5%;
  }
  .front-text {
    text-align: center;
    h1 {
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 700;
      font-size: 62px;
      text-align: center;
      letter-spacing: 0.05em;
  
      color: #ffffff;
    }
    h6 {
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 600;
      font-size: 30px;
      text-align: center;
      letter-spacing: 0.05em;
  
      color: #ffffff;
    }
    button {
      background: #00af00;
      margin: 20px 0px;
      color: white;
      width: 400px;
      height: 50px;
      border: none;
      font-size: 25px;
      font-weight: 700;
      border-radius: 50px;
      transition: 0.5s ease;
    }
  
    button:hover {
      color: black;
      background: white;
    }
  }
  .front-img {
    img {
      // margin-top: 5%;
      width: 639px;
      border: none;
      border-radius: 31px;
      -webkit-box-shadow: 7px 7px 0px 2px rgba(0, 175, 0, 0.88);
      -moz-box-shadow: 7px 7px 0px 2px rgba(0, 175, 0, 0.88);
      box-shadow: 7px 7px 0px 2px rgba(0, 175, 0, 0.88);
    }
  }

  @media only screen and (max-width: 991px) {
    .front-header{
      display: block !important;
      
    }
    .front-img {
      img{
        width: 100% !important;
      }
    }
  }