.about {
    margin-top: 60px;

    .container {
        .about__bottom {
            text-align: center;

            h1 {
                color: white;
                font-weight: 600;
                font-size: 40px;
            }

            .about__cards {
                margin-top: 60px;
                .about__card {
                    display: flex;
                    flex-direction: column;
                    text-align: center;
                    align-items: center;
                    padding: 30px;
                    margin: 0px 10px;
                    border: 5px solid white;
                    border-radius: 50px;
                    color: white;
                    h2{
                        font-weight: 700;
                    }
                    .card__bottom{
                        font-weight: 500;
                        margin-top: 15px;
                        p{
                            width: 100%;
                            font-size: 25px;
                        }
                    }
                }
                .about__card:nth-child(2){
                    border: 5px solid #009900;
                    color: #009900;
                }
            }
        }
    }
}