.container {
    .ts {
        padding: 30px 0px;
        z-index: 0;
        .card {
            background: transparent;
            margin-right: 10px;
            display: flex;
            justify-content: center;
            color: #009900;
            border: 3px solid #009900;
            border-radius: 20px;
            padding: 20px;
            cursor: pointer;
            transition: 0.5s ease;
            z-index: 9999;
            border-left: none;
            border-right: none;

            .card__body {
                display: flex;
                justify-content: center;
                flex-direction: column;
                align-items: center;

                img {
                    width: 150px;
                }

                .card__bottom {
                    h4 {
                        font-weight: 700;
                    }

                    p {
                        font-weight: 700;
                    }
                }
            }
        }

        .card:hover {
            background: rgba(255, 255, 255, 0.189);
            color: #009900;
            border-radius: 0px;

            h4 {
                font-weight: 700;
            }

            p {
                font-weight: 700;
            }
        }
    }
}