.cources {
    .container {
        .c_content {
            text-align: center;

            h1 {
                font-size: 60px;
                font-weight: 700;
            }

            .cards {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                grid-gap: 40px;
                align-items: center;
                text-align: center;
                margin-top: 40px;

                .cardo {
                    background: white;
                    border-radius: 30px;
                    width: 500px;
                    height: 440px;
                    padding: 25px;
                    -webkit-box-shadow: 0px -13px 0px 0px rgba(0, 175, 0, 1);
                    -moz-box-shadow: 0px -13px 0px 0px rgba(0, 175, 0, 1);
                    box-shadow: 0px -13px 0px 0px rgba(0, 175, 0, 1);
                    color: black;
                    transition: 0.5s ease;
                    margin: auto;
                    .acca_name {
                        font-weight: 700;
                        font-size: 40px;
                    }

                    .acca_p {
                        margin-top: 30px;
                        font-weight: 500;
                        font-size: 17px;
                    }

                    .acca_h3 {
                        font-weight: 600;
                    }

                    .acca_pr {
                        font-weight: 500;
                    }

                    .button {
                        background: #00AF00;
                        color: white;
                        border-radius: 10px;
                        font-weight: 600;
                        border: none;
                        font-size: 26px;
                        width: 250px;
                        height: 45px;
                    }
                }
                .cardo:hover {
                    cursor: pointer;
                    -webkit-box-shadow: 0px 0px 0px 6px rgba(0, 175, 0, 1);
                    -moz-box-shadow: 0px 0px 0px 6px rgba(0, 175, 0, 1);
                    box-shadow: 0px 0px 0px 6px rgba(0, 175, 0, 1);
                }

                .card {
                    background: white;
                    border-radius: 30px;
                    width: 500px;
                    height: 410px;
                    padding: 25px;
                    -webkit-box-shadow: 0px -13px 0px 0px rgba(0, 175, 0, 1);
                    -moz-box-shadow: 0px -13px 0px 0px rgba(0, 175, 0, 1);
                    box-shadow: 0px -13px 0px 0px rgba(0, 175, 0, 1);
                    color: black;
                    transition: 0.5s ease;
                    margin: auto;
                    .c_h2 {
                        font-weight: 700;
                        font-size: 40px;
                    }

                    .c_p {
                        margin-top: 30px;
                        font-weight: 500;
                        font-size: 28px;
                    }

                    .c_h3 {
                        font-size: 40px;
                        font-weight: 600;
                    }

                    .c_pr {
                        font-size: 24px;
                        font-weight: 600;
                    }

                    .button {
                        background: #00AF00;
                        color: white;
                        border-radius: 10px;
                        font-weight: 600;
                        border: none;
                        font-size: 26px;
                        width: 250px;
                        height: 45px;
                    }
                }

                .card:hover {
                    cursor: pointer;
                    -webkit-box-shadow: 0px 0px 0px 6px rgba(0, 175, 0, 1);
                    -moz-box-shadow: 0px 0px 0px 6px rgba(0, 175, 0, 1);
                    box-shadow: 0px 0px 0px 6px rgba(0, 175, 0, 1);
                }
            }
        }
    }
}

@media only screen and (max-width: 991px)  {
    .cards{
        display: block !important; 
        margin: 0 auto !important;
        .cardo{
            height: 100% !important;
            margin-top: 5% !important;
            width: 100% !important;
        }
        .card{
            height: 100% !important;
            margin-top: 5% !important;
            width: 100% !important;
        }
    }
    
}