.header {
  padding: 30px 0px;

  .container {
    .display-div {
      display: flex;
      justify-content: space-between;
      color: #ffff;

      .left {
        width: 50%;

        h1 {
          font-weight: 600;
          font-size: 50px;
        }

        .bottom {
          display: flex;
          flex-direction: column;
          align-items: start;
          text-align: center;

          .start {
            display: flex;
            flex-direction: column;
            align-items: center;

            button {
              background: #00af00;
              margin: 20px 0px;
              color: white;
              width: 400px;
              height: 50px;
              border: none;
              font-size: 25px;
              font-weight: 700;
              border-radius: 50px;
              transition: 0.5s ease;
            }

            button:hover {
              color: black;
              background: white;
            }

            a {
              color: white;
              margin-top: 20px;
              transition: 0.5s ease;
              padding: 10px;
              border-radius: 50px;
            }
            a:hover {
              color: black;
              background: white;
              border-radius: 50px;
              padding: 10px;
            }
          }
        }
      }

      .right {
        img {
          width: 550px;
          border: none;
          border-radius: 50px;
          -webkit-box-shadow: 7px 7px 0px 2px rgba(0, 175, 0, 0.88);
          -moz-box-shadow: 7px 7px 0px 2px rgba(0, 175, 0, 0.88);
          box-shadow: 7px 7px 0px 2px rgba(0, 175, 0, 0.88);
        }
      }
    }
  }
}

@media only screen and (max-width: 991px)  {
    .display-div{
        display: block !important; 
    }
    .right {
      img{
        width: 100% !important;
      }
    }
}
